<template>
  <div class="sidebar">
    <el-menu
      class="sidebar-el-menu"
      :default-active="onRoutes"
      :collapse="collapse"
      background-color="#324157"
      text-color="#bfcbd9"
      active-text-color="#20a0ff"
      unique-opened
      router
    >
      <template v-for="item in items">
        <el-badge :value="0" class="item" v-if="item.index=='Warning'" :key="">
        </el-badge>
        <template v-if="item.subs">
          
          <el-submenu
            :index="item.index"
            :key="item.index"
          >
            <template slot="title">
              <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu
                v-if="subItem.subs"
                :index="subItem.index"
                :key="subItem.index"
              >
                <template slot="title">{{ subItem.title }}</template>
                <el-menu-item
                  v-for="(threeItem,i) in subItem.subs"
                  :key="i"
                  :index="threeItem.index"
                >
                  {{ threeItem.title }}
                </el-menu-item>
              </el-submenu>
              <el-menu-item
                v-else
                :index="subItem.index"
                :key="subItem.index"
              >
                {{ subItem.title }}
              </el-menu-item>
            </template>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item
            :index="item.index"
            :key="item.index"
          >
            <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import bus from "../common/bus";
export default {
  data() {
    return {
      collapse: false,
      items: [
        {
          icon: "el-icon-all-home",
          index: "Index",
          title: "首页"
        },
        {
          icon: "el-icon-all-warning",
          index: "Warning",
          title: "预警信息"
        },
        {
          icon: "el-icon-all-economy",
          index: "GetContract",
          title: "持有仓单"
        },
        {
          icon: "el-icon-all-user",
          index: "CustomerInfo",
          title: "客户信息"
        },
        {
          icon: "el-icon-all-STATISTICS",
          index: "Statistics",
          title: "统计信息",
          subs: [
            {
              index: "permission",
              title: "仓单信息",
              subs: [
                {
                  index: "NotCleardBusiness",
                  title: "未结清业务"
                },
                {
                  index: "CleardBusiness",
                  title: "已结清业务"
                }
              ]
            },
            {
              index: "AccountRecord",
              title: "出账记录"
            },
            {
              index: "1",
              title: "预警信息",
              subs: [
                {
                  index: "CurrentWarning",
                  title: "当前预警"
                },
                {
                  index: "HistoryWarning",
                  title: "历史预警"
                }
              ]
            }
          ]
        }
      ]
    };
  },
  computed: {
    onRoutes() {
      return this.$route.path.replace("/", "");
    }
  },
  created() {
    // 通过 Event Bus 进行组件间通信，来折叠侧边栏
    bus.$on("collapse", msg => {
      this.collapse = msg;
    });
  }
};
</script>

<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 70px;
  bottom: 0;
  overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 170px;
}
.sidebar > ul {
  height: 100%;
}
.item {
  float: right;
  z-index: 999;
}
</style>



