<template>
    <div class="header">
        <!-- 折叠按钮 -->
        <div class="collapse-btn" @click="collapseChage">
            <i class="el-icon-menu"></i>
        </div>
        <div class="logo">仓单管理系统</div>
        <div class="header-right">
            <div class="header-user-con">
                <!-- 全屏显示 -->
                <!-- <div class="btn-fullscreen" @click="handleFullScreen">
                    <el-tooltip effect="dark" :content="fullscreen?`取消全屏`:`全屏`" placement="bottom">
                        <i class="el-icon-rank"></i>
                    </el-tooltip>
                </div> -->
                
                <!-- 用户名下拉菜单 -->
                <el-dropdown class="user-name" trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link">
                        {{username}} <i class="el-icon-caret-bottom"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item divided  command="modify">修改密码</el-dropdown-item>
                        <el-dropdown-item divided  command="loginout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <el-dialog title="修改密码" :visible.sync="modifyPwdVisible" width="20%">
                    <el-form :model="modifyPwdData" ref="form" :rules="rules" label-width="80px">
                        <el-form-item label="原密码" prop="oldPwd">
                            <el-input type="password" v-model.trim="modifyPwdData.oldPwd" placeholder="原密码"></el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="newPwd">
                            <el-input type="password" v-model.trim="modifyPwdData.newPwd" placeholder="新密码"></el-input>
                        </el-form-item>
                        <el-form-item label="确认密码" prop="newPwdConfirm">
                            <el-input type="password" v-model.trim="modifyPwdData.newPwdConfirm" placeholder="确认密码"></el-input>
                        </el-form-item>
                    </el-form>
                    <div
                        slot="footer"
                        class="dialog-footer"
                    >
                        <el-button @click="modifyPwdVisible = false">取 消</el-button>
                        <el-button type="primary" @click="onSubmit()">确 定</el-button>
                    </div>
                </el-dialog>
            </div>
        </div>
    </div>
</template>
<script>
    import bus from '../common/bus';

    export default {
        data() {
            //修改密码二次验证
            var validatePass = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请输入密码'));
				} else {
					if(this.modifyPwdData.newPwdConfirm !== '') {
						this.$refs.form.validateField('newPwdConfirm');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请再次输入密码'));
				} else if (value !== this.modifyPwdData.newPwd) {
					callback(new Error('两次输入的密码不一致'));
				} else {
					callback();
				}
			};
            return {
                
                collapse: false,
                fullscreen: false,
                name: 'username',
                message: 2,
                modifyPwdVisible: false,
                modifyPwdData:{
                    oldPwd:'',
                    newPwd:'',
                    newPwdConfirm:''
                },
                rules: {
                    newPwd: [
                        {required: true, validator: validatePass, trigger: 'blur' }
                    ],
                    newPwdConfirm: [
                        {required: true, validator: validatePass2, trigger: 'blur' }
                    ],
                }
            }
        },
        computed:{
            username(){
                let username = localStorage.getItem('aide_username');
                return username ? username : this.name;
            }
        },
        methods:{
            
            // 用户名下拉菜单选择事件
            handleCommand(command) {
                if(command == 'loginout'){
                    localStorage.removeItem('aide_username')
                    this.$router.push('/login');
                }else if(command == 'modify'){
                    this.modifyPwdVisible = true;
                }
            },
            // 侧边栏折叠
            collapseChage(){
                this.collapse = !this.collapse;
                bus.$emit('collapse', this.collapse);
            },
            // 全屏事件
            // handleFullScreen(){
            //     let element = document.documentElement;
            //     if (this.fullscreen) {
            //         if (document.exitFullscreen) {
            //             document.exitFullscreen();
            //         } else if (document.webkitCancelFullScreen) {
            //             document.webkitCancelFullScreen();
            //         } else if (document.mozCancelFullScreen) {
            //             document.mozCancelFullScreen();
            //         } else if (document.msExitFullscreen) {
            //             document.msExitFullscreen();
            //         }
            //     } else {
            //         if (element.requestFullscreen) {
            //             element.requestFullscreen();
            //         } else if (element.webkitRequestFullScreen) {
            //             element.webkitRequestFullScreen();
            //         } else if (element.mozRequestFullScreen) {
            //             element.mozRequestFullScreen();
            //         } else if (element.msRequestFullscreen) {
            //             // IE11
            //             element.msRequestFullscreen();
            //         }
            //     }
            //     this.fullscreen = !this.fullscreen;
            // }
            onSubmit(){
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        console.log("success")
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
        mounted(){
            if(document.body.clientWidth < 1500){
                this.collapseChage();
            }
        }
    }
</script>
<style scoped>
    .header {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        height: 70px;
        font-size: 22px;
        color: #fff;
    }
    .collapse-btn{
        float: left;
        padding: 0 21px;
        cursor: pointer;
        line-height: 70px;
    }
    .header .logo{
        float: left;
        width:250px;
        line-height: 70px;
    }
    .header-right{
        float: right;
        padding-right: 50px;
    }
    .header-user-con{
        display: flex;
        height: 70px;
        align-items: center;
    }
    .btn-fullscreen{
        transform: rotate(45deg);
        margin-right: 5px;
        font-size: 24px;
    }
    .btn-bell, .btn-fullscreen{
        position: relative;
        width: 30px;
        height: 30px;
        text-align: center;
        border-radius: 15px;
        cursor: pointer;
    }
    .btn-bell-badge{
        position: absolute;
        right: 0;
        top: -2px;
        width: 8px;
        height: 8px;
        border-radius: 4px;
        background: #f56c6c;
        color: #fff;
    }
    .btn-bell .el-icon-bell{
        color: #fff;
    }
    .user-name{
        margin-left: 10px;
    }
    .user-avator{
        margin-left: 20px;
    }
    .user-avator img{
        display: block;
        width:40px;
        height:40px;
        border-radius: 50%;
    }
    .el-dropdown-link{
        color: #fff;
        cursor: pointer;
    }
    .el-dropdown-menu__item{
        text-align: center;
    }
</style>



